/* --- LOGO IMAGE --- */

.logo-image {
    display: flex;
    justify-content: center;

    height: calc(10vh + 8vw);
}

.logo-image img {
    height: 100%;
}



/* --- NAVIGATION BAR --- */

.navigation-bar {
    height: 50px;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: hsl(0, 95%, 44%);
}



/* --- NAVIGATION MENU --- */

.navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;
}

.navigation-menu li {
    list-style-type: none;
    margin: 0 0.5rem;
}

.navigation-menu li a {
    display: block;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    color: hsl(0, 0%, 0%);
}

.navigation-menu li a:hover {
    color: hsl(0, 0%, 100%);
}



/* --- SOCIAL MEDIA ICONS --- */

.social-media-links ul {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0 0.25rem;
}

.social-media-links li {
    list-style-type: none;
    margin: 0 0.25rem;
}

.social-media-links img {
    display: block;
    height: 1.25rem;
    margin: auto;
    filter: opacity(60%);
}

.social-media-links img:hover {
    filter: opacity(100%);
}



/* --- MOBILE VIEW --- */

@media screen and (max-width: 730px) {
    .navigation-bar {
        justify-content: start;
        overflow: scroll;
        white-space: nowrap;
    }

    .navigation-bar::-webkit-scrollbar {
        display: none;
    }

    .navigation-menu ul {
        margin-left: 0.5rem;
    }

    .social-media-links ul {
        margin-right: 0.5rem;
    }

}